<template>
  <div class="container">

    <section class="hero has-text-centered">
      <div class="columns">
        <div class="column is-half is-offset-one-quarter">
          <nav class="level">
            <div class="level-left">
              TokenSupply
            </div>
            <div class="level-right">
              {{$root.p3dSupply}} {{$root.p3dName}}
            </div>
          </nav>
        </div>
      </div>
    </section>

    <div class="columns">
      <div class="column">
        <div class="tabs">
          <ul>
            <li @click="tab1=0" v-bind:class="{'is-active': tab1 === 0}"><a>买入{{$root.p3dName}}</a></li>
            <li @click="tab1=1" v-bind:class="{'is-active': tab1 === 1}"><a>卖出{{$root.p3dName}}</a></li>
          </ul>
        </div>
        <div v-show="tab1===0">
          <div class="field has-addons">
            <p class="control">
              <button class="button">
                <svg class="svg-inline--fa" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
                  <path fill="currentColor"
                        d="M0 405.3V448c0 35.3 86 64 192 64s192-28.7 192-64v-42.7C342.7 434.4 267.2 448 192 448S41.3 434.4 0 405.3zM320 128c106 0 192-28.7 192-64S426 0 320 0 128 28.7 128 64s86 64 192 64zM0 300.4V352c0 35.3 86 64 192 64s192-28.7 192-64v-51.6c-41.3 34-116.9 51.6-192 51.6S41.3 334.4 0 300.4zm416 11c57.3-11.1 96-31.7 96-55.4v-42.7c-23.2 16.4-57.3 27.6-96 34.5v63.6zM192 160C86 160 0 195.8 0 240s86 80 192 80 192-35.8 192-80-86-80-192-80zm219.3 56.3c60-10.8 100.7-32 100.7-56.3v-42.7c-35.5 25.1-96.5 38.6-160.7 41.8 29.5 14.3 51.2 33.5 60 57.2z"></path>
                </svg>
              </button>
            </p>
            <p class="control">
              <input class="input" type="text" v-model.number='buyEth' v-on:change="buyChange()">
            </p>
            <p class="control">
              <button class="button">
                ={{buyP3d}} {{$root.p3dName}}
              </button>
            </p>
          </div>
          <a class="button is-large" @click="buyToken()">购买</a>
        </div>
        <div v-show="tab1===1">
          <div class="field has-addons">
            <p class="control">
              <button class="button">
                <svg class="svg-inline--fa" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
                  <path fill="currentColor"
                        d="M311.9 260.8L160 353.6 8 260.8 160 0l151.9 260.8zM160 383.4L8 290.6 160 512l152-221.4-152 92.8z"/>
                </svg>
              </button>
            </p>
            <p class="control">
              <input class="input" type="text" v-model.number='sellP3d' v-on:change="sellChange()">
            </p>
            <p class="control">
              <button class="button">
                ={{sellEth}} ETH
              </button>
            </p>
          </div>
          <a class="button is-large" @click="sellToken()">卖出</a>
        </div>
      </div>

      <div class="column">
        <div class="tabs">
          <ul>
            <li @click="tab2=0" v-bind:class="{'is-active': tab2 === 0}"><a>Vault</a></li>
          </ul>
        </div>
        <div v-show="tab2===0">
          <h5 class="title is-5">{{$root.p3dName}}</h5>
          <div class="box">
            <nav class="level">
              <div class="level-left">
                您的{{$root.p3dName}}数量
              </div>
              <div class="level-right">
                <div class="level-item has-text-centered">
                  <div>
                    <p class="title">{{$root.p3dBalance}}</p>
                    <p class="heading">Tweets</p>
                  </div>
                </div>
              </div>
            </nav>
            <nav class="level">
              <div class="level-left">
                您的{{$root.p3dName}}分红
              </div>
              <div class="level-right">
                <div class="level-item has-text-centered">
                  <div>
                    <p class="title">{{$root.p3dDividends}}</p>
                    <p class="heading">Tweets</p>
                  </div>
                </div>
              </div>
            </nav>
            <a class="button is-large" @click="withdraw()">提起分红</a>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    name: 'Hourglass',
    data() {
      return {
        tab1: 0,
        tab2: 0,
        buyEth: 0,
        buyP3d: 0,
        sellP3d: 0,
        sellEth: 0,
      }
    },
    computed: {},
    created: function () {
      this.web3c();
    },
    methods: {
      web3c() {
        if (!$root.contP3d) return;
        var self = this;
        $root.contP3d.buyPrice(function (err, price) {
          if (err) return;
          self.buyEth = web3.fromWei(price.toNumber());
          self.buyChange();
        });
        self.sellP3d = 1;
        self.sellChange();
      },
      buyChange() {
        var self = this;
        this.$root.contP3d.calculateTokensReceived(web3.toWei(self.buyEth), function (err, token) {
          if (err) return;
          self.buyP3d = web3.fromWei(token).toNumber();
        });
      },
      buyToken() {
        if (!this.$root.$rqWeb3) {
          return;
        }

        var $root = this.$root;
        $root.contP3d.buy(0, {
          value: web3.toWei(this.buyEth),
          gasPrice: $config.gasPrice,
          gas: $config.gasLimit
        }, function (err) {
          if (!err) {
            $root.f3dGetSetAllDirty();
          }
        });
      },

      sellChange() {
        var self = this;
        this.$root.contP3d.calculateEthereumReceived(web3.toWei(self.sellP3d), function (err, eth) {
          if (err) return;
          self.sellEth = web3.fromWei(eth.toNumber());
        });
      },
      sellToken() {
        if (!this.$root.$rqWeb3 || this.sellP3d <= 0 || this.sellEth <= 0) {
          return;
        }

        var $root = this.$root;
        $root.contP3d.sell(web3.toWei(this.sellP3d), {
          gasPrice: $config.gasPrice,
          gas: $config.gasLimit
        }, function (err) {
          if (!err) {
            $root.f3dGetSetAllDirty();
          }
        });
      },

      withdraw() {
        if (!this.$root.$rqWeb3) {
          return;
        }

        $root.contP3d.withdraw({
          gasPrice: $config.gasPrice,
          gas: $config.gasLimit
        }, function (err, trans) {
          if (err) return;
          $root.f3dGetSetAllDirty();
        });
      },
    },
  }
</script>

<style>
</style>



